// ui.js
var Ui = pc.createScript('ui');
Ui.attributes.add('html', {
  type: 'asset',
  assetType: 'html'
});
Ui.attributes.add('css', {
  type: 'asset',
  assetType: 'css'
});
Ui.attributes.add('allData', {
  type: 'asset',
  assetType: 'json'
});
Ui.prototype.initialize = function () {

  Ui.boxData = this.allData;


  this.div = document.createElement("div")
  this.div.id = "trainDetails"
  this.div.innerHTML = this.html.resource || '';
  document.body.appendChild(this.div)

  var style = document.createElement("style")
  style.innerHTML = this.css.resource || '';
  document.head.appendChild(style)
  style = pc.createStyle(this.css.resource);
  document.head.appendChild(style);

  var script = document.createElement("script")
  script.src = "https://cdnjs.cloudflare.com/ajax/libs/mqtt/4.3.7/mqtt.min.js"
  document.head.appendChild(script)


  var tostiFyScript = document.createElement("script")
  tostiFyScript.type = "text/javascript"
  tostiFyScript.src = "https://cdn.jsdelivr.net/npm/toastify-js"
  document.head.appendChild(tostiFyScript)



  var tostiFyStyles = document.createElement("link")
  tostiFyStyles.rel = "stylesheet"
  tostiFyStyles.type = "text/css"
  tostiFyStyles.href = "https://cdn.jsdelivr.net/npm/toastify-js/src/toastify.min.css"
  document.head.appendChild(tostiFyStyles)


  // const client = mqtt.connect("wss://broker.hivemq.com:8000");
  // const client = mqtt.connect("wss://broker.mqttdashboard.com:8000/mqtt");
  //  const client = mqtt.connect("wss://broker.emqx.io:8084/mqtt");
  // const client = mqtt.connect("wss://test.mosquitto.org:8080");
  const client = mqtt.connect("wss://public:public@public.cloud.shiftr.io");
  //  console.log(mqtt)

  client.on('connect', function () {
    console.log("connected...... to sever")
    client.subscribe('demoTopic1', function (err) {
      console.log("subscribed")
      let connectionStatus = document.querySelector(".connection-status");
      connectionStatus.innerText = "Connected";
      // if (!err) {
      //   client.publish('presence', 'Hello mqtt')
      // }
    })
  })

  client.on('message', function (topic, message) {
    // message is Buffer

    var payload = message.toString()
    var json = JSON.parse(payload)
    console.log("topic", json)

    Toastify({
      text: json.message +" "+"for"+" location : "+Ui.boxData.resource[json.object].location,
      duration: 5000,

      gravity: "bottom", // `top` or `bottom`
      position: "center", // `left`, `center` or `right`
      stopOnFocus: true, // Prevents dismissing of toast on hover
      style: {
        background: json.color ? json.color :"#0284c7",
        padding:"1.2rem"
      },
      onClick: function () { } // Callback after click
    }).showToast();

    Ui.boxData.resource[json.object].temperature = json.temperature ? json.temperature : Ui.boxData.resource[json.object].temperature;
    Ui.boxData.resource[json.object].status = json.status;
    Ui.boxData.resource[json.object].lock = json.status.toLowerCase() === "unsafe package" ? "unsafe" : Ui.boxData.resource[json.object].lock;

    console.log(Ui.boxData.resource[json.object]);




    //  Ui.boxData.resource["Object_4.001"].temperature=json["Object_4.001"].temperature;
    //  console.log(Ui.boxData.resource["Object_4.001"].temperature)
    // client.end()
  })
};

