// orangeHighlight.js
var OrangeHighlight = pc.createScript('orangeHighlight');

// initialize code called once per entity
OrangeHighlight.prototype.initialize = function () {
  this.cameraEntity = this.entity;
  this.displayOrangeOutline();
};
// OrangeHighlight.prototype.update = function () {

//     // --- variables
//     this.cameraEntity = this.entity;
//     this.displayOrangeOutline();
// }
// update code called every frame
OrangeHighlight.prototype.displayOrangeOutline = function () {

  var glbFile = this.app.root.findByName("selected box")
  var entity = glbFile.children;
  for (var i = 0; i <= entity.length; i++) {
    this.app.fire('Ermis:objectOrangeOutline:add', entity[i]);
  }
};


