// highlight.js
var Highlight = pc.createScript('highlight');

// initialize code called once per entity
Highlight.prototype.initialize = function () {

    // --- variables
    this.cameraEntity = this.entity;
    this.displayRedOutline();
}
Highlight.prototype.update = function () {

    // --- variables
    this.cameraEntity = this.entity;
    this.displayRedOutline();
}

Highlight.prototype.displayRedOutline = function () {
    // console.log("display outline")
    var glbFile = this.app.root.findByName("selected box")
    var entity = glbFile.children;
    for (var i = 0; i <= entity.length; i++) {

        // console.log("entity[i]", entity[i]);
        this.app.fire('Ermis:objectRedOutline:add', entity[i]);
    }
};



