// video.js
var Video = pc.createScript('video');

Video.attributes.add('videoAsset', {
    title: 'Video Asset',
    description: 'MP4 video asset to play back on this video texture.',
    type: 'asset'
});

Video.attributes.add('videoUrl', {
    title: 'Video Url',
    description: 'URL to use if there is video asset selected',
    type: 'string'
});

Video.attributes.add('playEvent', {
    title: 'Play Event',
    description: 'Event that is fired as soon as the video texture is ready to play.',
    type: 'string',
    default: ''
});

// initialize code called once per entity
Video.prototype.initialize = function () {
    var app = this.app;

    // Create HTML Video Element to play the video
    var video = document.createElement('video');
    video.id = "myVideo"
    video.loop = false;

    // muted attribute is required for videos to autoplay
    video.muted = true;

    // critical for iOS or the video won't initially play, and will go fullscreen when playing
    video.playsInline = true;

    // needed because the video is being hosted on a different server url
    video.crossOrigin = "anonymous";

    // autoplay the video
    video.autoplay = true;

    // iOS video texture playback requires that you add the video to the DOMParser
    // with at least 1x1 as the video's dimensions
    var style = video.style;
   
    style.minWidth = '100vw';
    style.minHeight = '100vh';
    style.position = 'absolute';
    // style.top='0%';
    // style.left='0%';
    style.opacity = '1';
    style.zIndex = '1000';
    style.pointerEvents = 'none';

    document.body.appendChild(video);

    // Create a texture to hold the video frame data
    this.Video = new pc.Texture(app.graphicsDevice, {
        format: pc.PIXELFORMAT_R8_G8_B8,
        minFilter: pc.FILTER_LINEAR_MIPMAP_LINEAR,
        magFilter: pc.FILTER_LINEAR,
        addressU: pc.ADDRESS_CLAMP_TO_EDGE,
        addressV: pc.ADDRESS_CLAMP_TO_EDGE,
        mipmaps: true
    });
    this.Video.setSource(video);

    video.addEventListener('canplaythrough', function (e) {
        app.fire(this.playEvent, this.Video);
        video.play();
    }.bind(this));

    // set video source
    video.src = this.videoAsset ? this.videoAsset.getFileUrl() : this.videoUrl;
    console.log(this.videoAsset.getFileUrl())
    document.body.appendChild(video);
    video.load();

    this.on('destroy', function () {
        this.Video.destroy();
        video.remove();
    }, this);

    var vid = document.getElementById("myVideo");
    vid.onended = function () {
        document.body.removeChild(video)
        app.scenes.changeScene('Container_1 2');
    };
};

