// main.js

var Main = pc.createScript('main');

// initialize code called once per entity
Main.prototype.initialize = function () {
    //    this.app.fire('destroy');
    if (!this.entity.camera) {
        console.error('This script must be applied to an entity with a camera component.');
        return;
    }

    // Add a mousedown event handler
    this.app.mouse.on(pc.EVENT_MOUSEDOWN, this.mouseDown, this);

    // Add touch event only if touch is available
    if (this.app.touch) {
        this.app.touch.on(pc.EVENT_TOUCHSTART, this.touchStart, this);
    }
};
var script = document.createElement("script")
script.src = "https://cdnjs.cloudflare.com/ajax/libs/mqtt/4.3.7/mqtt.min.js"
document.head.appendChild(script);

Main.prototype.mouseDown = function (e) {
    this.doRaycast(e.x, e.y);
};

Main.prototype.touchStart = function (e) {
    // Only perform the raycast if there is one finger on the screen
    if (e.touches.length === 1) {
        this.doRaycast(e.touches[0].x, e.touches[0].y);
    }
    e.event.preventDefault();
};

Main.prototype.doRaycast = function (screenX, screenY) {
    // The pc.Vec3 to raycast from (the position of the camera)
    var from = this.entity.getPosition();
    // this.previous = undefined;
    var glbFile = this.app.root.findByName("selected box")
    if (glbFile) {
        var childEntities = glbFile.children;
        // console.log(childEntities)
        for (var i = 0; i < childEntities.length; i++) {
              childEntities[i].addComponent("collision", {
                    type: 'box',
                    //  halfExtents: new pc.Vec3(0.5, 0.5, 0.9)
                });  
        }
        // console.log("child",childEntities)
    }
    // var qrCode = this.app.root.findByName("selected QRcode")
    // if (qrCode) {
    //     var qrCodeChildren = qrCode.children;
    //     for (var j = 0; j < qrCodeChildren.length; j++) {
    //         qrCodeChildren[j].addComponent("collision", {
    //             type: 'box',
    //             halfExtents: new pc.Vec3(0.2, 0.2, 0.2)
    //         })
    //     }
    // }
    // The pc.Vec3 to raycast to (the click position projected onto the camera's far clip plane)
    var to = this.entity.camera.screenToWorld(screenX, screenY, this.entity.camera.farClip);

    // Raycast between the two points and return the closest hit result
    var result = this.app.systems.rigidbody.raycastFirst(from, to);

    // If there was a hit, store the entity
    if (result) {
        var hitEntity = result.entity;
        //console.log('You selected ' + hitEntity.name);
    }
};







